@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
}

@media screen and (max-width:700px){
    .Main{
        display: flex;
        flex-direction: column;
    }

    .Main{
        margin-top: 5px;
        width: 190px;
        height: 60px;
    }
}

@media screen and (min-width:1000px) {
    .Main{
        width: 50%;
    }
    
}

/* App */
.App {
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
/* Sidebar */
.Sidebar {
    width: 300px;
    min-width: 210px;
    height: 100%;
    background-color: #ffffff;
}

/* Wrap*/
.wrap{
    display: flex;
    flex-wrap: wrap;
}


/* Main */
.Main {
    display: flex;
    height: 100%;  
    width: 100%;
    background-image: url(/react-app-deploy/static/media/Bytela_BG.4021fd5a.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    overflow-y: auto;
    flex-wrap: wrap;
    display: flex;
}

/* User */
.User {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1rem;
    border: 1px solid #ebebeb;
}

.User .logo img {
    width: 60px;
    border-radius: 50%;
    border: 1px solid rgb(230, 226, 226);
}

.User .info p {
    font-size: 1.1rem;
    font-weight: bold;
}

.User .info a {
    text-decoration: none;
    display: flex;
    justify-content: flex-end;
}

/* Modal */
.Modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    z-index: 99;
}

.Modal .container {
    position: absolute;
    top: 25%;
    left: 50%;
    -webkit-transform: translate(-50%, -25%);
            transform: translate(-50%, -25%);
}

/* AddNew Todo */
.AddNewTodo {
    border-bottom: 1px solid #ebebeb;
}

.AddNewTodo .btn {
    padding: 1rem;
    width: 100%;
    height: 70px;
}

.AddNewTodo .btn button {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 15px;
    font-size: 1rem;
    background-color: rgb(245, 248, 43);
    color: rgb(245, 76, 104);
    font-weight: bold;
    cursor: pointer;
}

.AddNewTodo .btn button:hover {
    -webkit-filter: brightness(1.1);
            filter: brightness(1.1);
    transition: all 300ms ease;
}

/* AddNewTodo form */
.TodoForm {
    padding: 1rem 1rem calc(1rem + 40px) 1rem;
    width: 100%;
    background: white;
    border-radius: 6px;
}

.TodoForm .text input {
    padding: 1rem;
    width: 100%;
    margin-bottom: 0.5rem;
    border: none;
    font-size: 1.2rem;
}

.TodoForm .text input:focus {
    outline: none;
    border-bottom: 1px solid #c21e62;
}

.TodoForm .remind {
    display: flex;
    align-items: center;
    padding: 0.5rem;
}

.TodoForm p {
    margin-left: 10px;
}
.TodoForm .title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.TodoForm .pick-day {
    padding: 1.5rem 1.5rem 0 1.5rem;
}

.TodoForm .pick-time {
    padding: 1.5rem;
}

.TodoForm .pick-project {
    padding: 0.5rem;
}

.TodoForm .pick-project .projects {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 1rem 0 1rem;
}

.TodoForm .pick-project .projects .project {
    border: 1px solid #c21e62;
    border-radius: 5px;
    padding: 4px;
    margin: 5px;
    cursor: pointer;
}

.TodoForm .pick-project .projects .active {
    background-color: #c21e62;
    color: rgb(248, 248, 12);
}

.TodoForm .cancel {
    position: absolute;
    top: 0;
    right: 5px;
    cursor: pointer;
}

.TodoForm .confirm {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}

.TodoForm .confirm button {
    width: 100%;
    height: 40px;
    background-color: #c21e62;
    color: rgb(248, 248, 12);
    font-size: 1.1rem;
    border: none;
    border-radius: 0 0 4px 4px;
    cursor: pointer;
}

/* Calendar */

.Calendar {
    padding: 1rem;
    border-bottom: 1px solid #ebebeb;
}

.Calendar .header {
    display: flex;
    align-items: center;
}

.Calendar .header .title {
    display: flex;
    align-items: center;
    flex: 1 1;
}

.Calendar .header .title p {
    margin-left: 5px;
    font-size: 1.1rem;
    font-weight: bold;
}

.Calendar .header .btns span {
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Calendar .header .btns span:hover {
    background-color: #ebebeb;
    transition: all 300ms ease-in-out;
}

.Calendar .items {
    padding: 0.5rem;
}

.Calendar .items .item {
    padding: 0.25rem;
    margin: 5px;
    font-size: 1rem;
    cursor: pointer;
}

/* Projects */

.Projects {
    padding: 1rem;
    border-bottom: 1px solid #ebebeb;
}

.Projects .header {
    display: flex;
    align-items: center;
}

.Projects .header .title {
    display: flex;
    align-items: center;
    flex: 1 1;
}

.Projects .header .title p {
    margin-left: 5px;
    font-size: 1.1rem;
    font-weight: bold;
}

.Projects .header .btns span.edit {
    margin-right: 5px;
}

.Projects .header .btns {
    display: flex;
    align-items: center;
}

.Projects .header .btns span {
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Projects .header .btns span:hover {
    background-color: #ebebeb;
    transition: background 300ms ease-in-out;
}

.Projects .items {
    padding: 0.5rem;
}

/* Project */

.Project {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0.25rem;
    margin: 5px;
    font-size: 1rem;
}

.Project .name {
    cursor: pointer;
    word-break: break-all;
}

.Project .btns {
    display: flex;
    align-items: center;
}

.Project .btns .total-todos {
    position: absolute;
    right: 0;
    background: #ebebeb;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Project .btns .edit-delete {
    position: absolute;
    right: 0;
    display: flex;
}

.Project .btns .edit-delete span {
    border-radius: 50%;
    cursor: pointer;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 300ms ease-in-out;
}

.Project .btns .edit-delete span.edit:hover {
    background: #acffac;
}

.Project .btns .edit-delete span.delete:hover {
    background: #ffacac;
}

/* AddNewProject */

.AddNewProject .add-button span {
    margin-right: 5px;
}

/* Project Form */

.ProjectForm {
    background-color: white;
    padding: 1rem;
    border-radius: 4px;
    position: relative;
    padding-bottom: 40px;
}

.ProjectForm input {
    width: 100%;
    height: 40px;
    margin: 15px 0;
    border: none;
    font-size: 1.2rem;
}

.ProjectForm input:focus {
    outline: none;
    border-bottom: 1px solid #c21e62;
}

.ProjectForm button {
    position: absolute;
    bottom: 0;
    height: 40px;
    width: 50%;
    border: none;
    font-weight: bold;
    font-size: 1rem;
    transition: -webkit-filter 300ms ease-in-out;
    transition: filter 300ms ease-in-out;
    transition: filter 300ms ease-in-out, -webkit-filter 300ms ease-in-out;
}

.ProjectForm button.cancel {
    left: 0;
    background-color: #fbdbe9;
}

.ProjectForm button.confirm {
    right: 0;
    background-color: rgb(248, 248, 12);
}

.ProjectForm button:hover {
    -webkit-filter: brightness(1.1);
            filter: brightness(1.1);
    cursor: pointer;
}

/* Todos */

.Todos {
    width: 510px;
    height: 80%;
    background-color: white;

    position: absolute;
    bottom: 40px;
    left: 40px;

    border-radius: 4px;
    opacity: 0.9;

    padding:0 0.5rem 0.5rem 0.5rem;
    overflow-y: auto;
}

.Todos .selected-project {
    margin: 1rem 0;
    text-transform: capitalize;
    font-size: 1.2rem;
    font-weight: bold;
    padding-bottom: 2px;
    border-bottom: 1px solid #eeeeee;
}

.Todos .todos {
    overflow-y: auto;
}

/* Todo */
.Todo {
    padding: 0.5rem 1rem;
}

.Todo .todo-container {
    display: flex;
    align-items: center;
}

.Todo .todo-container .check-todo {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 15px;
}

.Todo .todo-container span {
    cursor: pointer;
}

.Todo .todo-container .text span {
    font-size: 0.8rem;
    color: #bebebe;
}

.Todo .todo-container .text {
    position: relative;
    flex: 1 1;
    margin: 0 10px;
    cursor: pointer;
}

.Todo .todo-container .text .line {
    height: 1px;
    background-color: #bebebe;
    position: absolute;
    top: 25%;
    width: 0;
    transition: width 250ms ease-in-out;
}

.Todo .todo-container .text .line-through {
    width: 100%;
}

.Todo .todo-container .add-to-next-day {
    margin-right: 10px;
}

/* Edit Todo */

.EditTodo {
    position: absolute;
    bottom: 40px;
    left: calc(100px + 450px + 100px);

    background-color: white;
    height: 80%;
    width: 510px;
    border-radius: 4px;
    opacity: 0.9;
    overflow-y: auto;
}

.EditTodo .header {
    margin: 1rem 0.5rem;

    font-size: 1.2rem;
    font-weight: bold;

    padding-bottom: 2px;
    border-bottom: 1px solid #eeeeee;
}

/* Next7Days */

.Next7Days .day {
    margin: 5px;
    display: flex;
}

.Next7Days .day .name {
    font-weight: bold;
    font-size: 1rem;
    text-transform: capitalize;
}

.Next7Days .day .total-todos {
    margin-left: 10px;
}


